.section-header {
  position: relative;

  &__left-top-img {
    position: absolute;
    top: -2.3rem;
    left: -4.7rem;
  }

  &__text {
    font-weight: 600;
    font-size: 3rem;
  }

  &__subtext {
    font-weight: 500;
    font-size: 3rem;
    color: #B7B7B7;
    padding-left: 2rem;
  }
}