.profile {
  width: 3rem;
  height: 3rem;
  background-color: #f5f5f5;
  border-radius: 0.375rem;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 1.4rem;

  &__icon {
    width: 100%;
    border-radius: 0.5rem;

    &:hover {
      opacity: 0.8;
    }
  }
}